.sidebar {
  background-color: #3d4f5d;
  box-sizing: border-box;
  min-height: 100vh;
  padding: 1em; }
  .sidebar h2 {
    color: #e4f1fb;
    margin-top: 0; }
  .sidebar .form-group {
    margin-bottom: 1em; }
    .sidebar .form-group input {
      background-color: #546674;
      border: 1px solid #3d4f5d;
      box-sizing: border-box;
      color: #e4f1fb;
      display: block;
      font-size: 1em;
      padding: 0.8em;
      width: 100%; }
    .sidebar .form-group label {
      color: #b0cadb;
      display: inline-block;
      font-size: 0.9em;
      margin-bottom: 0.2em; }
    .sidebar .form-group select {
      -webkit-appearance: none;
      background-color: #546674;
      border: 1px solid #3d4f5d;
      border-radius: 0;
      color: #e4f1fb;
      display: block;
      font-size: 1em;
      padding: 0.8em;
      width: 100%; }

.content {
  box-sizing: border-box;
  padding: 1em; }
  .content h2 {
    margin-top: 0; }

.quick-results {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 1em; }
  .quick-results .quick-result {
    border: 1px solid #3d4f5d;
    padding: 1em; }
    .quick-results .quick-result h3 {
      color: #546674;
      margin: 0; }
    .quick-results .quick-result .quick-result-figure {
      color: #3d4f5d;
      font-size: 36px; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

